.container {
  display: flex;
  flex-direction: column;
  position: relative;
}
.header {
  display: flex;
  margin-top: 56px;
  width: 100%;
  align-items: center;
  background-image: url("./img/img3.jpg");
  background-size: cover;
  height: calc(100vh - 56px);
}
.header_innerBox {
  margin-left: 15em;
  color: black;
}
.title {
  padding-bottom: 0.15em;
  border-bottom: 5px solid black;
  font-size: 4rem;
  text-shadow: -3px 0 white, 0 3px white, 3px 0 white, 0 -3px white;
}
.article {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.professor {
  display: flex;
  flex-direction: column;
  width: 80%;
  padding: 3em 0;
}
.professorProfile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
}
.profile {
  display: flex;
  margin-right: 3em;
}
.people1 {
  border-radius: 50%;
  width: 10em;
  margin-bottom: 1em;
  background-size: cover;
}
.profile li {
  font-size: 1rem;
}
.name {
  width: 100%;
  text-align: center;
  border-bottom: 3px solid rebeccapurple;
}
.students {
  width: 80%;
  display: flex;
  flex-direction: column;
}
.studentProfile {
  display: flex;
  flex-wrap: wrap;
}
.underline {
  width: 80%;
  height: 1px;
  background-color: green;
  margin: 10px 0;
}
.graduates {
  width: 80%;
  display: flex;
  flex-direction: column;
}
.graduationProfile {
  display: flex;
  flex-wrap: wrap;
}

@media screen and (max-width: 768px) {
  .header {
    justify-content: center;
    height: 50vh;
  }
  .header_innerBox {
    margin: 0;
  }
  .title {
    font-size: 2rem;
  }

  .professor {
    align-items: center;
  }
  .professorProfile {
    width: 100%;
  }
}
